export default {
  layout: "Dum-content",
  mounted() {
    this.getnotice();
  },
  data() {
    return {
      notices: [],
      notice: {
        title: "",
        description: "",
        new_image: "",
        slug: "",
      },
      errors: {},
      base_url: process.env.url
    };
  },
  methods: {
    getnotice() {
      this.$axios
        .$get("/notice/show")
        .then((res) => {
          this.notices = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    noticeEdit(id) {
      $("#noticeUpdate").modal("show");
      this.$axios
        .$get("/notice/edit/" + id)
        .then((res) => {
          this.notice = res;

        })
        .catch((err) => {
          console.log(err);
        });

    },
    noticeUpdate() {
      let formData = new FormData();
      formData.append('title', this.notice.title)
      formData.append('description', this.notice.description)
      if (this.notice.new_image) {
        formData.append('image', this.notice.new_image)
      }

      this.$axios
        .$post("/notice/update/" + this.notice.id, formData)
        .then((res) => {
          this.getnotice();
          $("#noticeUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deletenotice(id) {
      if (confirm("Are you sure to delete this notice?")) {
        this.$axios
          .$get("/notice/delete/" + id)
          .then((res) => {
            this.getnotice();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }

    },
    noticeStatus(id) {
      this.$axios
        .$get("/notice/status/" + id)
        .then((res) => {
          this.getnotice();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },









  }
};
